import { useRef, useEffect, useCallback } from 'react';
import { Box, Heading } from '@chakra-ui/react';
import Population from './AnimatedLineChartD3';
import classes from './animatedChartsD3.module.css';

function AnimatedLineChart() {
    let lineChart = useRef();
  
    const draw =  useCallback(() => {
      new Population(lineChart.current)
    }, [])
  
    useEffect(() => {
      draw();
          window.addEventListener('resize', draw);
          
          return () => {
              window.removeEventListener('resize', draw);
          }
      }, [draw])
  
    return (
      <Box  w='100%' marginLeft={'10px'}>
        <Heading as='h2' size='3x1' noOfLines={1} display="flex" justifyContent={'start'}>Animated Linechart</Heading>
        <div ref={lineChart} className={classes.chart}></div>
      </Box>
    )
  }
  
  export default AnimatedLineChart;

