import {
    Box,
    Heading,
    Text,
    Flex,
    Image,
    AspectRatio,
    OrderedList,
    ListItem,
    Code,
    Stack,
  } from '@chakra-ui/react';

import controllingLEDs from '../assets/images/controlling LEDs.png'
/*  
    int redPin = 10;
    int bluePin = 11;
    int greenPin = 12;

    void setup() {
        pinMode(redPin, OUTPUT);
        pinMode(bluePin, OUTPUT);
        pinMode(greenPin, OUTPUT);
    }

    void loop() {
        digitalWrite(redPin, HIGH);
        delay(100);
        digitalWrite(bluePin, HIGH);
        delay(500);
        digitalWrite(greenPin, HIGH);
        delay(1000);
    }
*/
function Robotics() {
  return (
    <Flex marginLeft={'10px'} flexDirection='column'>
      <Heading>Arduino projects</Heading>
        <OrderedList>
          <ListItem fontWeight={'bold'}>
            <Text >Controlling LED blink rate</Text>
            <Flex justifyContent={'left'}>
              <Image src={controllingLEDs} alt='naruto' objectFit='cover' />
              <Stack direction={'column'} spacing='0' marginLeft={'10px'}>
                <Code colorScheme="twitter" children="int redPin = 10;"></Code>
                <Code colorScheme="twitter" children="int bluePin = 11;"></Code>
                <Code colorScheme="twitter" children="int greenPin = 12;"></Code>
                <Code colorScheme="twitter" paddingTop="10px" children="void setup() {"></Code>
                <Code colorScheme="twitter" paddingLeft="20px" children="pinMode(redPin, OUTPUT);"></Code>
                <Code colorScheme="twitter" paddingLeft="20px" children="pinMode(bluePin, OUTPUT);"></Code>
                <Code colorScheme="twitter" paddingLeft="20px" children="pinMode(greenPin, OUTPUT);"></Code>
                <Code colorScheme="twitter" children="}"></Code>
                <Code colorScheme="twitter" paddingTop="10px" children="void loop() {"></Code>
                <Code colorScheme="twitter" paddingLeft="20px" children="digitalWrite(redPin, HIGH);"></Code>
                <Code colorScheme="twitter" paddingLeft="20px" children="delay(100);"></Code>
                <Code colorScheme="twitter" paddingLeft="20px" children="digitalWrite(bluePin, HIGH);"></Code>
                <Code colorScheme="twitter" paddingLeft="20px" children="delay(500);"></Code>
                <Code colorScheme="twitter" paddingLeft="20px" children="digitalWrite(greenPin, HIGH);"></Code>\
                <Code colorScheme="twitter" paddingLeft="20px" children="delay(1000);"></Code> 
                <Code colorScheme="twitter" children="}"></Code>              
              </Stack>
            </Flex>
          </ListItem>
        </OrderedList>
      </Flex>
    )
  }

export default Robotics;