import * as d3 from 'd3';
import "./lineChart.css";
import lineChartData from '../../assets/d3/lineChart.csv';

var width = 500;
var height = 300;
var margin = 50;

var lineOpacity = "0.25";
var lineOpacityHover = "0.85";
var otherLinesOpacityHover = "0.1";
var lineStroke = "1.5px";
var lineStrokeHover = "2.5px";

class AnimatedLineChartD3 {
  constructor(element) {
    let rect = element.getBoundingClientRect();
    width = rect.width;
    // isMobile = window.screen.width > 414 ? false: true;
    d3.select(element).select("svg").remove();
    this.draw(element);
  }

  draw(element) {
    // group the data: I want to draw one line per group
    var svg = d3.select(element)
  .append("svg")
    .attr("width", width + margin.left + margin.right)
    .attr("height", height + margin.top + margin.bottom)
  .append("g")
    .attr("transform",
          "translate(" + margin.left + "," + margin.top + ")");
  var sumstat = d3.group(lineChartData, d => d.name) // nest function allows to group the calculation per level of a factor
  console.log("check this >>> ", sumstat);
  // .key(function(d) { return d.name;})
  // .entries(lineChartData);

// Add X axis --> it is a date format
var x = d3.scaleLinear()
  .domain(d3.extent(lineChartData, function(d) { return d.year; }))
  .range([ 0, width ]);
svg.append("g")
  .attr("transform", "translate(0," + height + ")")
  .call(d3.axisBottom(x).ticks(5));

// Add Y axis
var y = d3.scaleLinear()
  .domain([0, d3.max(lineChartData, function(d) { return +d.n; })])
  .range([ height, 0 ]);
svg.append("g")
  .call(d3.axisLeft(y));

// color palette
var res = sumstat.map(function(d){ return d.key }) // list of group names
var color = d3.scaleOrdinal()
  .domain(res)
  .range(['#e41a1c','#377eb8','#4daf4a','#984ea3','#ff7f00','#ffff33','#a65628','#f781bf','#999999'])

// Draw the line
svg.selectAll(".line")
    .data(sumstat)
    .enter()
    .append("path")
      .attr("fill", "none")
      .attr("stroke", function(d){ return color(d.key) })
      .attr("stroke-width", 1.5)
      .attr("d", function(d){
        return d3.line()
          .x(function(d) { return x(d.year); })
          .y(function(d) { return y(+d.n); })
          (d.values)
      })
  }
}

export default AnimatedLineChartD3;